    .logo {
        display:inline-block;
        margin-bottom: 0
    }
}

.search-container {
    margin: 30px 0
}

.search-container .searchbox label {
    width: 100%;
    position: relative
}

.search-container .searchbox label:before {
    color: #ccc;
    content: "\f4a4";
    font-family: Ionicons;
    position: absolute;
    font-weight: lighter;
    bottom: 2px;
    left: 0;
    font-size: 22px
}

.search-container .searchbox .twitter-typeahead,.search-container .searchbox .typeahead {
    width: 100%;
    background: 0 0;
    border-bottom: solid 1px #fff
}

.search-container .searchbox .twitter-typeahead .form-control,.search-container .searchbox .typeahead .form-control {
    animation: fadein ease-in .5s;
    border: none
}

.search-container .searchbox .twitter-typeahead .searchbox__input.tt-hint,.search-container .searchbox .typeahead .searchbox__input.tt-hint {
    opacity: 0!important
}

