    color: #fff
}

.tt-dropdown-menu {
    animation: fadein ease-out .5s;
    min-width: 100%;
    margin-top: 2px;
    padding: 5px 0;
    background: rgba(0,0,0,.85);
    border-radius: 4px;
    box-shadow: 0 6px 12px rgba(0,0,0,.175);
    background-clip: padding-box;
    color: #fafafa
